import React,{Component} from 'react';
import axios from 'axios';
import {reactLocalStorage} from 'reactjs-localstorage';
import BorderColor from '@material-ui/icons/BorderColor';
import Close from '@material-ui/icons/Close';
import SweetAlert from 'sweetalert2-react';

class CategoryDetail extends Component{
    constructor(props){
        super(props);
        this.state = {
            category : [],
            show : false
        }

        this.loadHandler = this.loadHandler.bind(this);
        this.loadHandler();
    }

    loadHandler = () => {
        let token= reactLocalStorage.get('auth');
        let AuthStr = "Bearer ".concat(token);
        axios.get('http://13.229.249.101/dashboard/api/category/view', { headers: { Authorization: AuthStr } })
        .then(response => {
            const datas = response.data;
            this.setState({
                category : datas
            })
        })
        .catch((error) => {
            console.log('error ' + error);
        })
    }

    removeHandler = (id) => {
        const payload = {
            "id": id
        }
        let token= reactLocalStorage.get('auth');
        const AuthStr = "Bearer ".concat(token); 
        axios.post('http://13.229.249.101/dashboard/api/category/delete', payload, { headers: { Authorization: AuthStr } })
        .then((response) => {
            this.setState({
                show : true
            });
            this.loadHandler();
        })
        .catch((error) => {
            console.log(error);
        });  
        
    }

    render(){
        return (
            <tbody>
            <SweetAlert
                show={this.state.show}
                title="Category"
                text="Delete Category Success"
                onConfirm={() => 
                    this.setState({ show: false })

                }
            />
            {
            this.state.category.map((tag,i) => {
                return (
                    <tr key={i}>
                        <td className="mdl-data-table__cell--non-numeric">{tag.id}</td>
                        <td className="mdl-data-table__cell--non-numeric">{tag.brandName}</td>
                        <td className="mdl-data-table__cell--non-numeric">{tag.name}</td>
                        <td className="mdl-data-table__cell--non-numeric">{tag.description}</td>
                        <td>{tag.groupQty}</td>
                        <td className="mdl-data-table__cell--non-numeric">{tag.class}</td>
                        <td><a href={'#/admin/categoryedit/'+tag.id} className="text-dark"><BorderColor/></a> <a href="javascript:void(0)" onClick={()=> this.removeHandler(tag.id)} className="text-dark"><Close/></a></td>
                    </tr>
                )
            })
            }
            </tbody>
        );
    }
}

export default CategoryDetail;