import React from 'react';
import QueueAnim from 'rc-queue-anim';
import Button from '@material-ui/core/Button';
import AddIcon from '@material-ui/icons/Add';
import CategoryDetail from './CategoryDetail';

const Page = () => (
  <section className="container-fluid container-mw-xl chapter">
    <QueueAnim type="bottom" className="ui-animate">
      <div>
      <article className="article">
            <Button href="#/admin/categoryadd" variant="extendedFab" aria-label="delete" className="mt-5 mb-3" >
                <AddIcon/>
                Add Category
            </Button>
            <div className="box box-default table-box table-responsive mdl-shadow--2dp">
            <table className="mdl-data-table">
                <thead>
                <tr>
                    <th className="mdl-data-table__cell--non-numeric">ID</th>
                    <th className="mdl-data-table__cell--non-numeric">Brand</th>
                    <th className="mdl-data-table__cell--non-numeric">Name</th>
                    <th className="mdl-data-table__cell--non-numeric">Description</th>
                    <th>GroupQty</th>
                    <th className="mdl-data-table__cell--non-numeric">Class</th>
                    <th className="mdl-data-table__cell--non-numeric">Action</th>
                </tr>
                </thead>

                <CategoryDetail />
                
            </table>
            </div>
        </article>
      </div>
    </QueueAnim>
  </section>
);

export default Page;
